import React from 'react';
import bg from './images/bg.jpg';
import logo from './images/logo.png';
import "@blueprintjs/core/lib/css/blueprint.css";
import './App.css';
import { Navbar, Alignment, Menu, MenuItem, MenuDivider, Button, Intent, NavbarGroup, ButtonGroup } from "@blueprintjs/core";
import { Popover2 } from "@blueprintjs/popover2";
import { BrowserRouter, Routes, Route, Link } from 'react-router-dom';
import Contacts from './Contacts';
import Home from './Home';

export class App extends React.Component {
    constructor(props) {
        super(props);

        this.ItemIndex = -1;
    }

    componentDidMount() {
        document.title = "Сервис-48 Информ";
    }

    onContactClick() {
        document.getElementById("toContacts").click();
    }
    onClientClick() {
        document.getElementById("toClients").click();
    }

    onHomeClick() {
        document.getElementById("toHome").click();
    }

    render() {
        const projectsMenu = (
            <Menu>
                <MenuItem icon="record" text='ПК "МИСС"' />
                <MenuItem icon="record" text='ПК "СММ"' />
                <MenuItem icon="record" text='ПК "X-Price"' />
                <MenuItem icon="record" text='АС "Эффективность"' />
                <MenuItem icon="record" text='АС "Прогноз"' />
                <MenuDivider />
                <MenuItem icon="record" text='ПК "WEB-МИСС"' />
            </Menu>
        );

        const uslugiMenu = (
            <Menu>
                <MenuItem icon="record" text='Разработка ПО' />
                <MenuItem icon="record" text='Разработка сайтов' />
                <MenuItem icon="record" text='Онлайн услуги' />
            </Menu>
        );



        return (
            <BrowserRouter>
                <div className="App">
                    <header className="App-header">
                        <Navbar className="bp4-light" style={{ height: "35px", marginTop: "1px" }}>
                            <NavbarGroup align={Alignment.LEFT}>
                                <Navbar.Heading>
                                    <img src={logo} alt="text" height="30px" width="140px" style={{ border: "0px", marginTop: "3px" }}></img>
                                    {/* <img src={bg} height="200px" alt="The Changers" style={{ paddingTop: "3px" }}></img> */}
                                </Navbar.Heading>

                                <Navbar.Divider />
                            </NavbarGroup>
                            <NavbarGroup align={Alignment.CENTER}>
                                <div className="bp4-navbar-group bp4-align-right" id="navbarGroup">
                                    <ButtonGroup id="buGroup" fill={true} minimal={true} intent={Intent.NONE} large={false}>
                                        <Button text="О компании" id="buHome" icon="home" active={this.ItemIndex === 0 ? true : false} onClick={this.onHomeClick} />
                                        <Popover2 content={projectsMenu} placement="bottom-start">
                                            <Button text="Проекты" id="buProjects" rightIcon="caret-down" icon="projects" active={this.ItemIndex === 1 ? true : false} onClick={this.onAboutClick} />
                                        </Popover2>
                                        <Popover2 content={uslugiMenu} placement="bottom-start">
                                            <Button text="Услуги" icon="build" rightIcon="caret-down" active={this.ItemIndex === 2 ? true : false} onClick={this.onStudClick} />
                                        </Popover2>
                                        <Button text="Продукты" icon="layers" active={this.ItemIndex === 3 ? true : false} onClick={this.onAbiturClick} />
                                        <Button text="Клиенты" icon="person" active={this.ItemIndex === 4 ? true : false} onClick={this.onClientClick} />
                                        <Button text="Контакты" icon="envelope" active={this.ItemIndex === 6 ? true : false} onClick={this.onContactClick} />
                                    </ButtonGroup>
                                </div>
                            </NavbarGroup>
                        </Navbar>

                        <div id="links" style={{ display: "none" }}>
                            <Link className="menuItem" id="toHome" to="/">Главная</Link>
                            <Link className="menuItem" id="toAbout" to="/about">О нас</Link>
                            <Link className="menuItem" id="toProducts" to="/products">Продукты</Link>
                            <Link className="menuItem" id="toClients" to="/clients">Клиенты</Link>
                            <Link className="menuItem" id="toContacts" to="/contacts">Контакты</Link>
                        </div>
                    </header>
                    <Routes>
                        <Route path="/" element={<Home />}></Route>
                        <Route path="/contacts" element={<Contacts />}></Route>
                        <Route path="/clients" element={<div>АААААА</div>}></Route>
                    </Routes>
                </div>
            </BrowserRouter>
        );
    }
}

export default App;
